import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
// import 'bootstrap/dist/css/bootstrap.min.css';
import { useEffect, useRef, useState } from 'react';
import axios from 'axios'
import { Await, useNavigate } from 'react-router-dom'


// html    (nunjucks) node Ex08DB
//    (       )
//    >> DB  PK?      ..
//   

// const crypto = require('crypto')

// db not null     null  ...



function Join() {

  const nav = useNavigate()

  const idRef = useRef()
  const pwRef = useRef()
  const cpwRef = useRef()
  const nameRef = useRef()
  const nickRef = useRef()
  const addRef = useRef()



  
  
  const [userData, setUserData] = useState({})
  const [userId, setUserId] = useState({})
  
  
  
  
  // ID 
  const idCheck1 = (e1) => {
    e1.preventDefault();
    
    console.log(idRef.current.value);

    setUserId({ id: idRef.current.value })
    
  }

  useEffect(() => {   // 
    console.log('userId : ', userId.id)
    
    // console.log(' idCheck  : ', idCheck)
    //  id  undefined     5  
    if (userId.id !== undefined) {
      if (userId.id.length >= 5) {
        axios.post('http://localhost:8888/user/idcheck', {
          userId: userId
        })
        .then((res) => {

          console.log('   :', res.data.idCheck);
          if (res.data.idCheck === 'existed') {
            alert('  ',)
            idRef.current.value = ''
            idRef.current.focus()
            console.log(' , res.data.idCheck :', res.data.idCheck);
            
            
          } else if (res.data.idCheck === 'none') {
            alert('  ')
            pwRef.current.focus()
            console.log(' , res.data.idCheck :', res.data.idCheck);
            
            
          }
        })
        // undefined    
      } else if (userId.id.length < 5) {
        alert('  ')
        idRef.current.focus()
      }
    }
  }, [userId])
  
  
  
  // ...    
  const handleJoin = (e) => {
    e.preventDefault();

    console.log('handle Join Function'
      , idRef.current.value
      // , pwRef.current.value
      // , nameRef.current.value
      // , nickRef.current.value
      // , addRef.current.value
      );

    // form submit   


    setUserData({
      id: idRef.current.value,
      pw: pwRef.current.value,
      cpw: cpwRef.current.value,
      name: nameRef.current.value,
      nick: nickRef.current.value,
      add: addRef.current.value
    })
    console.log(userData);

  }
  
  useEffect(() => {
    console.log('userData : ', userData.id)
    /*useEffect  ,          .
          
         
        !*/
    
    
    if (userData.id !== undefined && userData.pw === userData.cpw) {
      if (userData.id.length >= 5
        && userData.id.length <= 20 && userData.pw.length >= 6
        && userData.name.length >= 2 && userData.nick.length >= 2 && userData.add.length >= 5) {

        //  id   undefined  pw, cpw    
        axios.post('http://localhost:8888/user/signup', {
          userData: userData
        })

          .then((res) => {
            console.log(' res', res.data.result);
            if (res.data.result == 'success') {
              alert(' ')
              // nav('/')
            } else if (res.data.result === 'duplicated') {
              alert(' ') //          
              console.log('  ')

              idRef.current.focus()
            }


          })
          .catch(() => {
            console.error('!')
          })
        //     (     )
      } else if (userData.id.length < 5) {
        alert('  !')
        idRef.current.focus()
      } else if (userData.pw.length < 6) {
        alert('  !')
        pwRef.current.value = ''
        cpwRef.current.value = ''
        pwRef.current.focus()
      } else if (userData.name.length < 2) {
        alert('  !')
        nameRef.current.focus()
      } else if (userData.nick.length < 2) {
        alert('  !')
        nickRef.current.focus()
      } else if (userData.add.length < 5) {
        alert(' ')
        addRef.current.focus()
      }
    } else if (userData.id !== undefined && userData.pw !== userData.cpw) {
      // id   pw, cpw     
      alert('  ')
      pwRef.current.value = ''
      cpwRef.current.value = ''
      pwRef.current.focus()
    }
  }, [userData])






  return (
    <div>
      <Form onSubmit={handleJoin} className='SignUpForm'>
     
       
        <br />
        {/* <div class="social-container">
          <a href="https://www.facebook.com/" target='_blind' class="social"><i class="fab fa-facebook-f"></i></a>
          <a href="https://www.google.com/" target='_blind' class="social"><i class="fab fa-google-plus-g"></i></a>
          <a href="https://www.instagram.com/" target='_blind' class="social"><i class="fab fa-square-instagram"></i></a>
        </div> */}

        <Row className='row1' >
          <Form.Group as={Col} controlId="formGridEmail">
            <Form.Label><p>ID</p></Form.Label>
            <Form.Control className='SLinput' type="text" placeholder="  (5 )" ref={idRef} />
            <Button onClick={idCheck1} className='idOverlap ' variant="primary" type="button" >  </Button>
          </Form.Group>

          <Form.Group as={Col} controlId="formGridPassword">
            <Form.Label><p>Password</p></Form.Label>
            <Form.Control className='SLinput' type="password" placeholder="  (6 )" ref={pwRef} />
          </Form.Group>

          <Form.Group as={Col} controlId="formGridPasswordck">
            <Form.Label><p>Double Check</p></Form.Label>
            <Form.Control className='SLinput' type="password" placeholder="  " ref={cpwRef} />
          </Form.Group>

          <Form.Group as={Col} controlId="formGridName">
            <Form.Label><p>NAME</p></Form.Label>
            <Form.Control className='SLinput' type="text" placeholder=" " ref={nameRef} />
          </Form.Group>

          <Form.Group as={Col} controlId="formGridNick2">
            <Form.Label><p>NICKNAME</p></Form.Label>
            <Form.Control className='SLinput' type="text" placeholder=" " ref={nickRef} />
          </Form.Group>

          <Form.Group className="mb-3" controlId="formGridAddress1">
            <Form.Label><p>e-mail</p></Form.Label>
            <Form.Control className='SLinput' placeholder="" ref={addRef} />
          </Form.Group>
          <Form.Group id="formGridCheckbox">
            <Form.Check type="checkbox" label="    ." />
          </Form.Group>

          <Button variant="primary" type="submit" className='idOverlap SLBtn'>
           
          </Button>
        </Row>

      </Form>

    </div>

  );
}

export default Join;
