import React, { useEffect, useState } from 'react'
import Logo from '../../Images/RealYourLog.svg'

import { Link } from 'react-router-dom'

import CreatePost from '../Navigation/CreatePost';
import ChatStart from '../../js/ChatStart';
import Cart from '../../page/Cart';




const Head = () => {


  const [userId, setUserId] = useState('');

  //  id      
  const checkLogin = () => {
    if (sessionStorage.getItem('id') !== null) {
      setUserId(true);
    } else {
      setUserId(false)
    }
  };


  //      id      

  //      id      
  const handleLogout = () => {
    sessionStorage.clear();
    window.location.reload();
    setUserId('');
  };

  //     id     
  useEffect(() => {
    checkLogin();
  }, [userId]);

  // useEffect(()=>{

  // },[userId])



  return (
    <>
      <ul className='HeadGnbBox'>
        <li>
          {userId ? (
            <Link to='/Mypage'></Link>
          ) : (
            null
          )}
        </li>
        <li>
          {userId ? (
            <Link to='/Modification'></Link>
          ) : (
            null
          )}
        </li>
        {userId ? (
          <li className='HeadGnb'><Link to='/' onClick={handleLogout}></Link></li>
        ) : (
          <li className='HeadGnb'><Link to='/SignUpLogin'>/</Link></li>
        )}
      </ul>


      {/* <div className='HeadGnb'><Link to='/SignUpLogin'><p>/</p></Link></div> */}
      <ul className='head'>
        <li className='headBox'>
          <div className='LogoBox'><Link to='/'><img src={Logo} alt="" className='LogoImg' /></Link></div>
          <div className='daangsp'><p></p></div>
          <div className='inputBox'><input type="text" className='search' placeholder='  ' /></div>
          <div><i class="fa-solid fa-magnifying-glass"></i></div>
        </li>
        <li className='headNv'>
          <CreatePost />
        </li>
        <li>
          <a href="#footer1"><p className='CPCtt' data-hover=""><span></span></p></a>
        </li>
        <li>
          <Link to='/cart'><p className='CPCtt CPCttLove' data-hover=""><span className='CPjjim'></span></p></Link>
        </li>
      </ul>
    </>
  )
}

{/* <button class="btn2"><i class="fa-solid fa-bars"></i></button> */ }
export default Head
