
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
const CreatePost = () => {

  const [userId, setUserId] = useState('');

  //  id      
  const checkLogin = () => {
    if (sessionStorage.getItem('id') !== null) {
      setUserId(true);
    } else {
      setUserId(false)
    }
  };


  //      id      

  //      id      
  const handleLogout = () => {
    sessionStorage.removeItem('id');
    window.location.reload();
    setUserId('');
  };

  //     id     
  useEffect(() => {
    checkLogin();
  }, [userId]);

  // useEffect(()=>{

  // },[userId])

  return (
    <>

      {userId ? (
        
        <Link to='/PostForm'><p className='CPCtt' data-hover=""><span></span></p></Link>
      
      ) : (
        null
      )}

     
    </>


  )
}

export default CreatePost