import React from 'react'
import axios from 'axios';
import { useEffect } from 'react';

const RealPayment = () => {


    const IMP = window.IMP;

    useEffect(() => {
      const script1 = document.createElement('script');
      script1.src = 'https://code.jquery.com/jquery-1.12.4.min.js';
      script1.type = 'text/javascript';
      document.head.appendChild(script1);
  
      const script2 = document.createElement('script');
      script2.src = 'https://cdn.iamport.kr/js/iamport.payment-1.2.0.js';
      script2.type = 'text/javascript';
      document.head.appendChild(script2);
  
      script2.onload = () => {
        const IMP = window.IMP;
        IMP.init("imp31853445");
      };
    }, []);

 

    const RealPayment = () => {
      const IMP = window.IMP;
      IMP.request_pay(
        {
          pg: 'kcp',
          pay_method: 'card',
          merchant_uid: '57008833-33004',
          name: ' 10kg',
          amount: 1004,
          buyer_email: 'Iamport@chai.finance',
          buyer_name: ' ',
          buyer_tel: '010-1234-5678',
          buyer_addr: '  ',
          buyer_postcode: '123-456'
        }, function(rsp) {
          if (rsp.success) {   
            // axios HTTP 
            axios({
              url: "3000/Detaill",
              method: "post",
              headers: { "Content-Type": "application/json" },
              data: {
                imp_uid: rsp.imp_uid,
                merchant_uid: rsp.merchant_uid
              }
            }).then((data) => {
              //   API  
            })
          } else {
            alert(` .  : ${rsp.error_msg}`);
          }
        }
      );
    };
    

  return (

    <li> <a href="#" class="item">
    <div class="icon">ii</div>
    <div class="text" onClick={RealPayment}><span class="circle"></span></div>
    <div class="right"> </div>
  </a>
  </li>


  )
}

export default RealPayment